<template>
<!--  html区域 template里面只能有一个div-->
  <div class="about" style="min-height:100%" >
<!--  这里用的是 element-ui官网 => Layout Container => sample
      具体布局参考element-ui官方文档
      基本所有界面 template div 不变把以下组件进行修改就行
      -->
    <el-container style="min-height:100%;" >

      <el-aside :width="sideWidth+'px'"  style="background-color: rgb(238, 241, 246);overflow-y: scroll" >
<!--    :collapse-transition="false" 关闭标签动画效果，解决导航栏收缩卡顿问题-->
        <el-menu :default-openeds="['1', '3']"
            style="min-height:100%;"
            background-color="rgb(48,65,86)"
            text-color="#fff"
            active-text-color="#ffdo4b"
            :collapse="isCollapsed"
            :collapse-transition="false">
<!--          添加目录头-->
          <div style="height:60px;line-height: 60px;text-align: center">
            <img src="../assets/logo.png" style="width:20px;position: relative; top:5px;margin-left: 5px">
            <b style="color:white" v-show="LogoTextShow">后台管理界面</b>
          </div>
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">Navigator One</span>
            </template>
            <el-menu-item-group>
              <template slot="title">Group 1</template>
              <el-menu-item index="1-1">Option 1</el-menu-item>
              <el-menu-item index="1-2">Option 2</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="Group 2">
              <el-menu-item index="1-3">Option 3</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="1-4">
              <template slot="title">Option4</template>
              <el-menu-item index="1-4-1">Option 4-1</el-menu-item>
            </el-submenu>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">Navigator Two</span>
            </template>
            <el-menu-item-group>
              <template slot="title">Group 1</template>
              <el-menu-item index="2-1">Option 1</el-menu-item>
              <el-menu-item index="2-2">Option 2</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="Group 2">
              <el-menu-item index="2-3">Option 3</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="2-4">
              <template slot="title">Option 4</template>
              <el-menu-item index="2-4-1">Option 4-1</el-menu-item>
            </el-submenu>
          </el-submenu>
          <el-submenu index="3" >
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">Navigator Three</span>
            </template>
            <el-menu-item-group>
              <template slot="title">Group 1</template>
              <el-menu-item index="3-1">Option 1</el-menu-item>
              <el-menu-item index="3-2">Option 2</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="Group 2">
              <el-menu-item index="3-3">Option 3</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="3-4">
              <template slot="title">Option 4</template>
              <el-menu-item index="3-4-1">Option 4-1</el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </el-aside>

      <el-container>
        <el-header style="font-size: 12px;line-height:60px;border-bottom: 1px solid #ccc;display:flex ">
          <div style="flex:1;font-size: 25px">
            <span :class="collapseBtnClass" style="cursor: pointer" @click="collapse"></span>
          </div>
          <el-dropdown style="cursor:pointer">
            <!--        <i class="el-icon-setting" style="margin-right: 15px"></i>-->
            <span>Tom</span> <i class="el-icon-arrow-down" style="margin-left:5px"></i>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>View</el-dropdown-item>
              <el-dropdown-item>Add</el-dropdown-item>
              <el-dropdown-item>Delete</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>

        </el-header>

        <el-main>
          <!--      增删改查 <i>的class 里使用不同风格的图标 type element里封装好的样式 -->
          <div style="padding: 10px 0;float: left">
            <el-button type="primary">insert
              <i class="el-icon-circle-plus-outline"></i>
            </el-button>
            <el-button type="danger">Delete
              <i class="el-icon-remove-outline"></i>
            </el-button>
            <el-button type="primary">input
              <i class="el-icon-download"></i>
            </el-button>
            <el-button type="primary">output
              <i class="el-icon-upload2"></i>
            </el-button>
          </div>
          <!--      搜索框-->
          <div  style="padding: 10px 0; float: right">
            <el-input  style="width:150px;float: left"></el-input>
            <el-button type="primary"  style="float: left">Primary</el-button>
          </div>

















        <div id="viewDiv" class="map"></div>
          <!--      表格主题-->
          <el-table :data="tableData" >
<!--            table li的prop和数据表的表头 相同-->
            <el-table-column type="index" align="left" width="90"  label="编号" > </el-table-column>
            <el-table-column prop="UserName" label="UserName" width="140"></el-table-column>
            <el-table-column prop="Password" label="Password" width="120" v-model="password">
            <!-- <el-input  width="120" v-model="scope.row.password"></el-input> -->
            </el-table-column>
            <el-table-column prop="Type" label="Type"></el-table-column>
            <el-table-column >              
              <template slot-scope="scope">
                <el-button type="success" @click="editUser(scope.row)">editor<i class="el-icon-edit-outline"></i></el-button>
                <el-button type="danger" @click="deleteUser(scope.row)">delete<i class="el-icon-remove-outline"></i></el-button>
              </template>
              
            </el-table-column>
          </el-table>




          <!--      分页效果栏 Pagination-->
          <div  style="padding: 15px ;align=right">
            <div class="block">
<!--              @尺寸变化和当前页面 点击触发函数 pageNum pageSize total 与fetch查询到的数据库数据进行绑定-->
              <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pageNum"
                  :page-sizes="[5,10,15,20]"
                  :page-size="pageSize"
                  :hide-on-single-page="false"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="total">
              </el-pagination>
            </div>
          </div>

        </el-main>

      </el-container>

    </el-container>


  </div>
</template>
<script>
// import {Map} from "@arcgis/core/Map";
import Map from "@arcgis/core/Map";
import MapView from "@arcgis/core/views/MapView";
import VectorTileLayer from "@arcgis/core/layers/VectorTileLayer";

export default {
  name: 'adminPanel',

  data() {
    return {
      tableData:[],//列表数据
      total:10,//总计
      pageNum:1,
      pageSize:10,
      // tableData: Array(10).fill(item),
      collapseBtnClass:'el-icon-s-fold',//折叠图标的类型根据点击事件 collapse发生改变
      isCollapsed:false, //false展开true折叠
      sideWidth:200,//侧导航栏宽度
      LogoTextShow:true,//文本是否显示
      password:""
    }
  },
  
  created(){
  //  请求分页查询数据
    this.load()
  },
  mounted() {
    const map = new Map({
      basemap: "streets-vector",
    });
 
    const view = new MapView({
      container: "viewDiv",
      map: map,
    });
    view.zoom = 2;
  },


  methods:{//写一些界面常见方法，用于请求和传递数据
    collapse(){ //点击收缩按钮触发 菜单栏收缩
      this.isCollapsed=!this.isCollapsed;
      if(this.isCollapsed){
        this.sideWidth=64
        this.collapseBtnClass='el-icon-s-unfold'
        this.LogoTextShow=false
      }else{
        this.sideWidth=200
        this.collapseBtnClass='el-icon-s-fold'
        this.LogoTextShow=true

      }
    },

    editUser(row){
      console.log(this.Map)

    },


    load(){
      var that = this;
      this.axios.get('/usersapi/'+this.pageNum+"/"+this.pageSize)
      .then(function (res) {
          that.tableData=res.data;
        })
        .catch(function (error) {
          console.log(error);
        });
       this.axios.get('/usersapi/dataList')
      .then(function (res) {    
          that.total=res.data.length;
        })     
    },


    handleSizeChange(pageSize){
      console.log("pageSize:"+pageSize)
      this.pageSize=pageSize 
      this.load()
    },


    handleCurrentChange(pageNum){
      console.log("pageNum:"+pageNum)
      this.pageNum=pageNum
      this.load()
      
    },

    deleteUser(row){
      if(confirm("确定删除吗？删除是不可逆的操作！")){
        this.axios.delete('/usersapi'+row.UserName)
        .then((res)=>{console.log(res)})
        .then(this.load())
        .then(window.location.reload())
      }
    }

  }

}
</script>

<style>
.map {
  width: 100%;
  height: 90vh;
}
</style>


